* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  
  body {
    padding: 0;
    margin: 0;
  }
  
  #maintenance {
    position: relative;
    height: 100vh;
    overflow: hidden;
  }
  
  #maintenance .maintenance {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  
  .maintenance {
    max-width: 100%;
    width: 100%;
    text-align: center;
    line-height: 1.4;
  }
  
  .maintenance .maintenance-404 {
    position: relative;
    width: 100%;
    height: 200px;
    margin: 0px auto 50px;
  }
  
  .maintenance .maintenance-404>div:first-child {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: #ffa200;
    border: 10px dashed #000;
    border-left: none;
    border-right: none;
  }
  
  .maintenance .maintenance-404>div:first-child:before {
    content: '';
    position: absolute;
    left: -5px;
    right: -5px;
    bottom: -5px;
    top: -5px;
    -webkit-box-shadow: 0px 0px 0px 5px rgba(0, 0, 0, 0.1) inset;
            box-shadow: 0px 0px 0px 5px rgba(0, 0, 0, 0.1) inset;
    border-radius: 5px;
  }
  
  .maintenance .maintenance-404 h1 {
    font-family: 'Cabin', sans-serif;
    color: #FFF;
    font-weight: 700;
    margin: 0;
    font-size: 70px;
    position: absolute;
    top: 35%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    left: 50%;
    text-align: center;
    height: 40px;
    width: 100%;
  }
  
  .maintenance h2 {
    font-family: 'Cabin', sans-serif;
    font-size: 33px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 7px;
  }
  
  .maintenance p {
    font-family: 'Cabin', sans-serif;
    font-size: 16px;
    color: #8F8F8F;
    font-weight: 400;
  }
  
  .maintenance a {
    font-family: 'Cabin', sans-serif;
    display: inline-block;
    padding: 5px 5px;
    border: none;
    color: #00BFA5;
    font-size: 16px;
    font-weight: 700;
    text-decoration: none;
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
  }
  
  